.search {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1 1;
    max-width: 440px;
    width: 100%;
    background: white;
    position: relative;
    top: 0;
    bottom: 0;
}

.search .container {
    width: 100%;
    box-sizing: border-box;
    padding: 0;
    flex: 1 1;
}

.search .category-filter {
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    border-bottom: 1px solid var(--gray1);
}

.search .category-filter .category-wrapper {
    display: flex;
    justify-content: flex-start;
}

.search .category-filter .category-wrapper > :first-child {
    padding-left: 1rem;
}

.search .category-filter .category-wrapper > :last-child {
    padding-right: 1rem;
}

.search .category-filter .category-wrapper .category {
    width: fit-content;
}

.search .category-filter .category-wrapper .category.selected {
    border-bottom: 2px solid var(--gray6);
    font-weight: bold;
}

.search .category-filter .category-wrapper .category .name {
    margin: 1rem 1rem 1rem 1rem;
    word-break: keep-all;
    white-space: nowrap;
}